body {
  font-family: 'Roboto', "Arial", sans-serif;
  background-color: #fff;
  color: #000000;
  position: relative;
  max-width: 100%;
  overflow-x: hidden;
}
html {
  overflow-x: hidden;
}
h1 {
  font-size: 50px;
  line-height: 50px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  margin: 0;
}
h2 {
  font-size: 43px;
  line-height: 50px;
  color: #3B363C;
  font-weight: bold;
  margin: 0;
}
h3 {
  font-size: 30px;
  margin: 0;
  font-weight: 300;
  line-height: 35px;
  color: #3B363C;
}
h4 {
  font-weight: normal;
  font-size: 19px;
  line-height: 25px;
  color: #3B363C;
  margin: 0;
}
.sroll-top {
  display: none;
}
p {
  font-size: 14px;
  line-height: 21px;
}
.description-item__text {
  font-weight: lighter;
}
a {
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
a {
  transition: .3s;
  color: #FFF;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}
a:hover {
  border-bottom: 1px solid #eae440;
  color: #eae440;
}
.yellow-color {
  color: #eae440;
}
.blackout {
  position: relative;
}
.blackout:before {
  background: rgba(92, 92, 80, 0.6);
  width: 63%;
  position: absolute;
  content: '';
  right: 0;
  top: 0;
  bottom: 0;
}
.blackout:after {
  background: #3B363C;
  width: 37%;
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  bottom: 0;
}
.title-line {
  position: relative;
}
.title-line:before {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  width: 100px;
  height: 2px;
  background-color: #eae440;
  left: 0;
}
.smal-title-line {
  position: relative;
}
.smal-title-line:before {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  width: 30px;
  height: 2px;
  background-color: #eae440;
  left: 0;
}
.long-title-line {
  position: relative;
}
.long-title-line:before {
  position: absolute;
  content: '';
  display: block;
  bottom: 0;
  width: 200px;
  height: 2px;
  background-color: #eae440;
  left: 0;
}
.btn {
  font-size: 14px;
  line-height: 21px;
  font-weight: bold;
  color: #000;
  padding: 6px 35px 8px 35px;
  background-color: #F3F0A2;
  border: none;
  transition: .3s;
  white-space: nowrap;
}
.btn:hover {
  background-color: #e4e195;
}
.grey-rect {
  position: relative;
  z-index: 5;
}
.grey-rect:before {
  content: '';
  position: absolute;
  display: block;
  width: 262px;
  height: 135px;
  border: 12px solid rgba(0, 0, 0, 0.03);
  top: -24px;
  left: 112px;
  z-index: -1;
}
.grey-rect--dark-bg:before {
  border: 12px solid rgba(255, 255, 255, 0.05);
}
.yellow-square {
  position: relative;
}
.yellow-square:before {
  content: '';
  display: block;
  position: absolute;
  width: 96px;
  height: 100px;
  top: 60px;
  left: -96px;
  background-color: #eae440;
}
.galery-overlay {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  left: 0;
  background-color: #eae440;
  opacity: 0;
  width: 80%;
  height: 80%;
  margin: 10%;
  transition: .3s;
}
.galery-overlay:hover {
  opacity: .8;
}
.galery-overlay:before {
  position: absolute;
  display: block;
  content: '';
  width: 20%;
  height: 20%;
  background-image: url("../img/icons/eye.svg");
  background-size: 100% 100%;
  top: 38%;
  left: 38%;
}
@media (max-width: 767px) {
  h2 {
    font-size: 25px;
    line-height: 33px;
  }
  h3 {
    font-size: 18px;
    line-height: 24px;
  }
  h4 {
    font-size: 14px;
    line-height: 21px;
  }
  .grey-rect:before {
    display: block;
    width: 230px;
    height: auto;
    border: 5px solid rgba(0, 0, 0, 0.03);
    top: -8px;
    left: 37px;
    bottom: -10px;
  }
  .btn {
    font-size: 12px;
    text-transform: uppercase;
  }
  .yellow-square:before {
    width: 33px;
    height: 33px;
    top: -16px;
    left: 30px;
  }
  .title-line {
    position: relative;
  }
  .title-line:before {
    position: absolute;
    content: '';
    display: block;
    bottom: 0;
    width: 85px;
    height: 2px;
    background-color: #eae440;
    left: 0;
  }
}
.page-header {
  padding-top: 60px;
  background-color: #3B363C;
  background-image: url(../img/bg-1.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  margin-bottom: 48px;
  z-index: 200;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-header {
    background-image: url("../img/bg-1@2x.jpg");
  }
}
.page-header__wrapper {
  position: relative;
  max-width: 1140px;
  margin: auto;
  z-index: 5;
}
.page-header__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 116px;
}
.page-header__contacts {
  display: flex;
  flex-direction: column;
  color: #fff;
  padding-left: 20px;
  padding-top: 7px;
  width: 15%;
  position: relative;
}
.page-header__contacts:before {
  position: absolute;
  display: block;
  content: '';
  top: 10px;
  left: -5px;
  width: 19px;
  height: 22px;
  background-image: url('../img/icons/phone.svg');
  background-size: cover;
}
.page-header__phone {
  font-size: 21px;
  line-height: 27px;
  font-weight: bold;
}
.page-header__phone-description {
  opacity: .5;
  font-size: 12px;
  line-height: 18px;
}
.page-header__logo {
  position: relative;
  border-bottom: none;
  width: 200px;
}
.page-header__logo:hover {
  border-bottom: none;
}
.page-header__logo:before {
  position: absolute;
  content: 'by OKSANA SKVORTOVA';
  display: block;
  bottom: -12px;
  right: 15px;
  font-size: 8px;
  opacity: .5;
}
.page-header__bottom-gallery {
  position: relative;
  z-index: 2;
  display: flex;
}
.page-header__title {
  text-align: left;
  font-weight: 300;
  width: 100%;
  padding-bottom: 37px;
  position: relative;
  margin-bottom: 16px;
}
.page-header__title--color {
  color: #eae440;
}
.page-header__title-wrapper {
  display: flex;
  max-width: 674px;
  margin-left: 100px;
  flex-wrap: wrap;
  position: relative;
  padding-bottom: 40px;
}
.page-header__title-wrapper:before {
  position: absolute;
  content: '';
  display: block;
  top: -30px;
  width: 437px;
  height: 252px;
  border: 12px solid rgba(87, 87, 87, 0.18);
  left: 25%;
}
.header-gallery-list {
  display: flex;
  margin-left: 70px;
  margin-bottom: -100px;
  margin-top: -87px;
}
.header-gallery-item {
  margin-left: 28px;
}
.galery-link {
  position: relative;
  display: block;
  border-bottom: none;
}
.galery-link:hover {
  border-bottom: none;
}
.reed-more-wrapper {
  display: flex;
  justify-content: space-between;
  width: 600px;
}
.reed-more,
.reed-more-video {
  position: relative;
  width: 45%;
  font-size: 20px;
  line-height: 50px;
  font-weight: 300;
  z-index: 55;
}
.reed-more-video a:before {
  position: absolute;
  display: block;
  content: '';
  top: 0;
  left: -65px;
  width: 55px;
  height: 55px;
  background-image: url('../img/icons/youtube.svg');
  background-size: cover;
}
.fixed {
  position: fixed;
  box-sizing: border-box;
  top: 0;
  left: 0;
  width: 100%;
  max-height: 65px;
  z-index: 50;
  background-color: rgba(60, 55, 61, 0.8);
}
.fixed .sroll-top {
  display: block;
  position: fixed;
  bottom: 20px;
  right: 15px;
  border: 1px solid #e9e344;
  border-radius: 50%;
  padding: 2px 0;
}
.fixed .sroll-top:hover {
  border-bottom: none;
}
.fixed .sroll-top__home-ico {
  fill: #EAE440;
}
.fixed .fixed-container {
  position: relative;
  max-width: 1140px;
  margin: 0 auto;
}
.fixed + .page-header__title-wrapper {
  padding-top: 219px;
  z-index: -1;
}
.fixed .main-nav {
  padding-top: 20px;
}
.fixed .page-header__logo:before {
  display: none;
}
